@mixin border-define($top, $right, $bottom, $left, $color: black, $style: solid) {
    //$list: $top $right $bottom $left;
    $min: 1px;

    //$topx: $top * $min;
    $topx: $top * $min;
    $rightx: $right * $min;
    $bottomx: $bottom * $min;
    $leftx: $left * $min;

    border-top: $topx solid $color;
    border-right: $rightx solid $color;
    border-bottom: $bottomx solid $color;
    border-left: $leftx solid $color;
    border-style: $style;
}

/* border box everything in the universe, and what comes before and after, by Paul Irish 
http://www.paulirish.com/2012/box-sizing-border-box-ftw/ */

html {
    box-sizing: border-box;
}

*, *:before, *:after {
    box-sizing: inherit;
}

