    justify-content: space-between;
}

/* adjusts the size of first letter in h1.  I had already set up a span to uppercase it and float it,  so I stuckc with it */

h1 span {
    font-size: 3em;
    text-transform: uppercase;
}

nav ul {
    margin-top: 0;
    margin-left: 0;
    padding: 0;
    display: flex;
    flex-flow: row nowrap;
    list-style: none;
    justify-content: flex-start;
    margin-bottom: 0;

    //align-items: center;
}

nav ul li:first-of-type {
    @include border-define(2, 1, 1, 1);
}

nav ul li {
    flex-basis: 10%;

    //flex: .5 1 10%;
    padding: 0;
    margin: 0;
    background: yellow;

    @include border-define(2, 1, 1, 0);
}

nav li a {
    text-decoration: none;
    width: 100%;
    display: block;
    padding: 0;
    margin: 0;
    text-align: center;
}

/* this is setting up the transparent background image in the header */

header {
    height: 10em;
    width: 100%;
    position: relative;
    letter-spacing: .4em;
    border-bottom: 1px solid black;
    z-index: 0;
    overflow: hidden;
}

header:after {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    content: "";
    background: url("../img/background2x_1200.png") no-repeat;
    opacity: 0.6;
    z-index: -3;
    display: block;
    background-position: 50% 50%;
    background-size: cover;
}

